

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

html {
  background-image: url(bg.jpeg);
  height: 100vh;
  background-repeat: 'no-repeat';
  background-size: 'cover';

}
.todo-app {
  display: flex;
  flex-direction: column;
  width: 520px;
  min-height: 600px;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, .75 );
  backdrop-filter: blur( 4px );
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

h1 {
  margin: 32px 0;
  font-size: 24px;
  color: rgb(4, 22, 37);
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border: 2px solid rgb(205, 140, 224);
  outline: none;
  width: 320px;
  border-radius: 4px;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, .75 );
  backdrop-filter: blur( 4px );
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.todo-input::placeholder {
  color: rgb(4, 22, 37);
}

.todo-button {
  padding: 16px;
  border: none;
  cursor: pointer;
  outline: none;
  text-transform: capitalize;
  color: antiquewhite;
  background-color: rgb(4, 22, 37);
  margin-left: 10px;
  border-radius: 100px;
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, .75 );
}

.todo-input.edit {
  border: 2px solid crimson;
}

.todo-button.edit {
  background: crimson;
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: rgb(4, 22, 37);
  padding: 16px;
  width: 90%;
  margin-top: 10px;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, .75 );
  backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
 color: rgb(4, 22, 37);
}

.edit-icon {
 color: rgb(4, 22, 37);
}

